@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@400;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu',
    'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

.principal-nav{display:flex;justify-content:center;align-items:center;background:#d9cc1e;padding:0;height:110px;font-family:"Raleway",sans-serif}.tab-items{display:flex;justify-content:space-around;width:auto;align-items:center;background-color:#c09cd9;height:60px}.tab-items .nav-items{margin:.5rem;width:-webkit-fit-content;width:-moz-fit-content;width:fit-content}.tab-items .nav-items a{all:unset}.tab-items .nav-items a .link-items{margin:1rem 1rem;padding:.5rem 1rem;background-color:#d9cc1e;color:#353ff2;border-radius:.8rem;cursor:pointer}.tab-items .nav-items .link-items:hover{background-color:#7d60a6;color:#d9cc1e}
.navbar-items{margin-top:.5rem;font-weight:bold;display:flex;justify-content:center}.navbar-items #text-navbar{font-family:"Raleway",sans-serif;font-size:20px;width:10rem;color:#c09cd9;border:.05rem solid #d9cc1e;border-radius:.8rem}.navbar-items .navbar-movies{font-family:"Raleway",sans-serif;background-color:#7d60a6;border-radius:.8rem}.navbar-items .check-items{background-color:#d9cc1e;padding:.5rem 2rem;color:#353ff2;border-radius:.8rem}.alert{font-family:"Raleway",sans-serif}.alert .tittle-modal{background-color:#c09cd9;color:#f0f8ff}.alert #body-modal{text-align:center;font-size:20px;font-weight:bold;background-color:#f0f8ff;color:darkred}.alert .footer-modal{justify-content:center;background-color:#c09cd9}.alert .footer-modal #button-modal{font-size:20px;color:#f0f8ff;background-color:rgba(0,0,0,0);border:2px solid #d9cc1e}.alert .footer-modal #button-modal:hover{background-color:#d9cc1e;color:#353ff2;border-color:#f0f8ff}
.container-main{margin-top:1rem;display:grid;grid-template-rows:repeat(auto-fit, 1fr);grid-template-columns:repeat(4, 1fr);background-color:#7503a6;margin-right:.5rem;margin-left:.5rem;border-radius:.8rem;overflow:auto}.container-main .movie{display:flex;flex-direction:column;justify-content:space-evenly;align-items:center;border:2px solid #7d60a6;margin:1rem;border-radius:.8rem}.container-main .movie h2{font-family:"Roboto",sans-serif;color:#f2d64b;font-weight:bold}.container-main .movie img{margin-top:.5rem;border-radius:.8rem}.container-main .movie p{text-align:left;font-family:"Raleway",sans-serif;color:#d5e7f2;margin:.5rem;font-weight:bold}.container-main .movie p span{margin-left:.3rem;color:#f2d64b}.container-main .movie ul{margin-top:.5rem;color:#f2d64b}.container-main .movie .overview{text-align:left;margin:.5rem;color:#f2d64b}.container-main .movie .overview h4{text-align:center;color:#d5e7f2;font-weight:bold}
.container-genres{display:flex;width:auto;justify-content:center;margin:.05rem}.container-genres .genre-list{display:grid;grid-template:repeat(4, 1fr)/repeat(5, 1fr);justify-items:flex-start;list-style:none;background-color:#d9cc1e;border-radius:.8rem;padding:1rem}.container-genres .genre-list .list-items-genre{margin:.05rem .5rem;color:#353ff2}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

